import { Col, Container, Row } from "react-bootstrap";
import tv from '../../Assets/Images/TV-2.png';
import CustomLink from "../CustomLink/Index";

export default function ContentTVCircle({
  titleSmaller,
  title,
  content,
}){
  return (<section className={`content_tv_circle ${titleSmaller===true?'title_smaller':''}`}>
    <Container>
      <Row>
        <Col lg={12}>
          {/*<h2>The Liberty<br/>Blockchain Platform</h2>*/}
          <h2>{title}</h2>
          <div className={`circle_content ${(content===undefined || content===null?'just_tv':'')}`}>
            <div className="the_tv">
              <img src={tv} alt="The TV" />
            </div>
            {
              content!==undefined && content!==null?<div className="content">
                {content}
              </div>:null
            }
            {/*<div className="content">
              <p>Liberty Blockchain platform will provide a service that does not resell or commoditize your information. The type of Web 3 applications we will build on our blockchain will offer decentralization, data provenance and transparency. This lack of centralized control means better security and data autonomy for our users.</p>
              <div className="buttons_holder">
                <CustomLink className="btn btn-success" to="./BuyALightNode">BUY A LIGHT NODE</CustomLink>
                <CustomLink className="btn btn-primary" to="./BuyANode">BUY A NODE</CustomLink>
              </div>
            </div>*/}
          </div>
        </Col>
      </Row>
    </Container>
  </section>);
}